.bookDescription {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;

	background: rgba(255, 255, 255, 0.1);

	padding: 1rem;
	margin: 2rem;
}

.bookDescription img {
	width: 100%;
	height: auto;

	flex: 0 1 20rem;
}

.bookDescription > div {
	display: flex;
	flex-direction: column;
	padding: 0 1rem;

	flex: 1 0 20rem;
}

.bookDescription > div h2 {
	font-family: "Roboto Slab";
	font-size: 2.2rem;
	margin-bottom: 0.2rem;
}

.bookDescription > div h3 {
	font-family: "Roboto Slab";
	font-size: 1.4rem;
	margin-bottom: 1rem;
}

.bookDescription a {
	display: flex;
	align-items: center;

	margin-top: auto;
	padding-top: 0.5rem;
	align-self: flex-end;
	color: #ff9900;

	transition: all 0.2s ease-out;
}

.bookDescription a:hover {
	transform: perspective(1px) scale(1.02, 1.05) translateZ(0);
}

.bookDescription svg {
	margin-right: 0.3rem;
}

.bookCharacteristics {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.bookCharacteristics div {
	display: flex;
	justify-content: space-between;
	flex: 1 1 25rem;

	background: rgba(255, 255, 255, 0.1);
	padding: 0.5rem;
	margin: 0.5rem;
	border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.bookCharacteristics div p {
	margin-left: 0.5rem;
}

.bookReviews {
	display: flex;
	flex-direction: column;

	background: rgba(255, 255, 255, 0.1);

	padding: 1rem;
	margin: 2rem;
}

.bookReviews h2 {
	font-family: "Roboto Slab";
	font-size: 2rem;
}

.bookReviews a {
	padding: 0.5rem;
	margin: 0.5rem 0;
	border-top: 1px solid rgba(255, 255, 255, 0.3);
	border-bottom: 1px solid rgba(255, 255, 255, 0.3);

	transition: all 0.2s ease-out;
}

.bookReviews a:hover {
	background-color: rgba(255, 255, 255, 0.1);
	transform: perspective(1px) scale(1.00775, 1.05) translateZ(0);
}

.bookReviews div {
	display: flex;
	justify-content: space-between;

	margin-bottom: 0.5rem;
}

.handleFavorite {
	display: flex;
	justify-content: space-between;
	align-items: center;

	margin-bottom: 1rem;
}

.handleFavorite svg {
	padding: 0.2rem;

	font-size: 2rem;
	color: #83da11;
	cursor: pointer;

	transition: all 0.2s ease-out;
}

.handleFavorite svg:hover {
	transform: perspective(1px) scale(1.1) translateZ(0);
}
