.bookRow {
	display: flex;
	flex-direction: column;

	padding: 1rem;
	margin: 2rem 0;
}

.bookRow h1 {
	color: whitesmoke;
	padding-left: 1rem;
	font-weight: bold;
	font-size: 2rem;
}

.bookList {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}

.bookItem {
	display: flex;
	flex-direction: column;
	position: relative;
	flex: 0 1 15rem;

	overflow: hidden;
	text-align: center;

	margin: 1rem;

	color: whitesmoke;
	box-shadow: 0 1px 1px rgba(255, 255, 255, 0.1), 0 2px 2px rgba(255, 255, 255, 0.1), 0 4px 4px rgba(255, 255, 255, 0.1), 0 8px 8px rgba(255, 255, 255, 0.1), 0 16px 16px rgba(255, 255, 255, 0.1);
	border-radius: 0.3rem;
	transition: all 0.7s ease-out;
}

.bookItem:hover div {
	margin-top: 0;
}

.bookItem img {
	height: 22rem;
	object-fit: cover;
}

.bookItem div {
	display: flex;
	flex-direction: column;
	justify-content: center;
	flex-grow: 1;

	position: absolute;
	margin-top: 200%;
	width: 100%;
	height: 100%;

	background: rgba(0, 0, 0, 0.3);
}

.bookItem h2 {
	font-family: "Roboto Slab";
	font-size: 1.4rem;
}

.bookItem h3 {
	font-family: "Roboto Slab";
	font-size: 1.1rem;
}
