header {
	display: flex;
	flex-direction: column;
	justify-content: space-between;

	padding: 2rem;

	background: rgba(255, 255, 255, 0.05);
}

header nav {
	display: flex;
	justify-content: space-evenly;
}

header h2 {
	text-transform: uppercase;
	font-size: 1.6rem;
	font-weight: bold;
}

header form {
	display: flex;

	margin-top: 1rem;

	color: black;
}

header form input {
	width: 100%;
	padding: 0 1rem;

	color: whitesmoke;
	font-size: 1rem;

	background: rgba(255, 255, 255, 0.1);
	border: none;
}

header form button {
	color: whitesmoke;
	background: none;
	border: none;
}

header form button svg {
	padding: 0.3rem;
	margin-left: 0.6rem;
	font-size: 2rem !important;
}
