@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Coda&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: space-between;
	        justify-content: space-between;

	padding: 2rem;

	background: rgba(255, 255, 255, 0.05);
}

header nav {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
}

header h2 {
	text-transform: uppercase;
	font-size: 1.6rem;
	font-weight: bold;
}

header form {
	display: -webkit-flex;
	display: flex;

	margin-top: 1rem;

	color: black;
}

header form input {
	width: 100%;
	padding: 0 1rem;

	color: whitesmoke;
	font-size: 1rem;

	background: rgba(255, 255, 255, 0.1);
	border: none;
}

header form button {
	color: whitesmoke;
	background: none;
	border: none;
}

header form button svg {
	padding: 0.3rem;
	margin-left: 0.6rem;
	font-size: 2rem !important;
}

.bookRow {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;

	padding: 1rem;
	margin: 2rem 0;
}

.bookRow h1 {
	color: whitesmoke;
	padding-left: 1rem;
	font-weight: bold;
	font-size: 2rem;
}

.bookList {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}

.bookItem {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	position: relative;
	-webkit-flex: 0 1 15rem;
	        flex: 0 1 15rem;

	overflow: hidden;
	text-align: center;

	margin: 1rem;

	color: whitesmoke;
	box-shadow: 0 1px 1px rgba(255, 255, 255, 0.1), 0 2px 2px rgba(255, 255, 255, 0.1), 0 4px 4px rgba(255, 255, 255, 0.1), 0 8px 8px rgba(255, 255, 255, 0.1), 0 16px 16px rgba(255, 255, 255, 0.1);
	border-radius: 0.3rem;
	transition: all 0.7s ease-out;
}

.bookItem:hover div {
	margin-top: 0;
}

.bookItem img {
	height: 22rem;
	object-fit: cover;
}

.bookItem div {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-grow: 1;
	        flex-grow: 1;

	position: absolute;
	margin-top: 200%;
	width: 100%;
	height: 100%;

	background: rgba(0, 0, 0, 0.3);
}

.bookItem h2 {
	font-family: "Roboto Slab";
	font-size: 1.4rem;
}

.bookItem h3 {
	font-family: "Roboto Slab";
	font-size: 1.1rem;
}

.bookDescription {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: space-around;
	        justify-content: space-around;

	background: rgba(255, 255, 255, 0.1);

	padding: 1rem;
	margin: 2rem;
}

.bookDescription img {
	width: 100%;
	height: auto;

	-webkit-flex: 0 1 20rem;

	        flex: 0 1 20rem;
}

.bookDescription > div {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	padding: 0 1rem;

	-webkit-flex: 1 0 20rem;

	        flex: 1 0 20rem;
}

.bookDescription > div h2 {
	font-family: "Roboto Slab";
	font-size: 2.2rem;
	margin-bottom: 0.2rem;
}

.bookDescription > div h3 {
	font-family: "Roboto Slab";
	font-size: 1.4rem;
	margin-bottom: 1rem;
}

.bookDescription a {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;

	margin-top: auto;
	padding-top: 0.5rem;
	-webkit-align-self: flex-end;
	        align-self: flex-end;
	color: #ff9900;

	transition: all 0.2s ease-out;
}

.bookDescription a:hover {
	-webkit-transform: perspective(1px) scale(1.02, 1.05) translateZ(0);
	        transform: perspective(1px) scale(1.02, 1.05) translateZ(0);
}

.bookDescription svg {
	margin-right: 0.3rem;
}

.bookCharacteristics {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.bookCharacteristics div {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-flex: 1 1 25rem;
	        flex: 1 1 25rem;

	background: rgba(255, 255, 255, 0.1);
	padding: 0.5rem;
	margin: 0.5rem;
	border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.bookCharacteristics div p {
	margin-left: 0.5rem;
}

.bookReviews {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;

	background: rgba(255, 255, 255, 0.1);

	padding: 1rem;
	margin: 2rem;
}

.bookReviews h2 {
	font-family: "Roboto Slab";
	font-size: 2rem;
}

.bookReviews a {
	padding: 0.5rem;
	margin: 0.5rem 0;
	border-top: 1px solid rgba(255, 255, 255, 0.3);
	border-bottom: 1px solid rgba(255, 255, 255, 0.3);

	transition: all 0.2s ease-out;
}

.bookReviews a:hover {
	background-color: rgba(255, 255, 255, 0.1);
	-webkit-transform: perspective(1px) scale(1.00775, 1.05) translateZ(0);
	        transform: perspective(1px) scale(1.00775, 1.05) translateZ(0);
}

.bookReviews div {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;

	margin-bottom: 0.5rem;
}

.handleFavorite {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	-webkit-align-items: center;
	        align-items: center;

	margin-bottom: 1rem;
}

.handleFavorite svg {
	padding: 0.2rem;

	font-size: 2rem;
	color: #83da11;
	cursor: pointer;

	transition: all 0.2s ease-out;
}

.handleFavorite svg:hover {
	-webkit-transform: perspective(1px) scale(1.1) translateZ(0);
	        transform: perspective(1px) scale(1.1) translateZ(0);
}

.bookSearch {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;

	margin-top: 1rem;
}

.errorMessage {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	padding: 1rem;
	margin: 1rem 0;

	font-size: 4rem;
}
/* General */

* {
	margin: 0;
	padding: 0;

	font-family: inherit;
	color: inherit;
	font-weight: inherit;
	transition: inherit;
	box-sizing: border-box;
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
}

b {
	font-weight: 500;
}

li {
	list-style: none;
	padding: 0.4rem;
}

input[type="search"] {
	padding: 0.3rem 4rem 0.3rem 0.3rem;
}

input[type="search"]:focus {
	outline: #717171;
	vertical-align: top;
}

body {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;

	margin: auto;
	max-width: 90rem;

	font-family: "Roboto", sans-serif;
	font-weight: 300;

	background: rgba(0, 0, 0, 0.95);
	color: whitesmoke;
}

.mainSection {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	padding-bottom: 2rem;

	background: #000;
}

